"use client";

import { useScramble } from "@/hooks/use-scramble";

type TextScrambleProps = {
  text: string;
  className?: string;
};

export function TextScramble({ text, className }: TextScrambleProps) {
  const { ref, replay } = useScramble({ text });

  return (
    <span
      ref={ref}
      onMouseOver={replay}
      onFocus={replay}
      className={className}
    />
  );
}
