"use client";

import { useRef, useEffect } from "react";

// Adapted from https://codepen.io/Hyperplexed/pen/rNrJgrL
const chars = "!<>-_\\/[]{}—=+*^?#________";

export const useScramble = ({
  text,
  speed = 0.5,
  scramble = 8,
  step = 1,
  seed = 1,
  seedMin = 10,
  seedMax = 30,
}) => {
  const ref = useRef<HTMLElement>(null);
  const raf = useRef(0);
  const state = useRef({
    frame: 0,
    resolve: () => {},
  });

  const random = (min: number, max: number) => {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  };

  const scrambleText = (str: string) => {
    let result = "";
    for (let i = 0; i < str.length; i++) {
      if (Math.random() > 0.5) {
        result += str[i];
      } else {
        result += chars[random(0, chars.length - 1)];
      }
    }
    return result;
  };

  const replay = () => {
    if (ref.current) {
      ref.current.style.opacity = '1';
      state.current.frame = 0;
      animate();
    }
  };

  const animate = () => {
    cancelAnimationFrame(raf.current);
    raf.current = requestAnimationFrame(() => {
      if (!ref.current) return;

      if (state.current.frame < scramble) {
        ref.current.innerText = scrambleText(text);
        state.current.frame += speed;
        animate();
      } else {
        ref.current.innerText = text;
        cancelAnimationFrame(raf.current);
        state.current.resolve();
      }
    });
  };

  useEffect(() => {
    if (!ref.current) return;
    
    ref.current.style.opacity = '0';
    const timeoutId = setTimeout(() => {
        if (ref.current) {
            ref.current.style.opacity = '1';
            animate();
        }
    }, 1000); // Delay before starting the animation

    return () => {
      cancelAnimationFrame(raf.current);
      clearTimeout(timeoutId);
    };
  }, [text]);

  return { ref, replay };
};
